import React from "react";
import AppMode from "./../AppMode.js";

class CoursesTable extends React.Component {
  constructor() {
    super();
  }

  //ToDo: populate table with League information
  //Also add flag for commmissioner to appear differently somehow
  renderTable = () => {
    let table = [];
    for (let r = 0; r < this.props.users.length; ++r) {
      table.push(
        <tr key={r}>
          <td>{this.props.league[r].users.teamName}</td>
          <td>{this.props.league[r].users.win}</td>
          <td>{this.props.league[r].users.loss}</td>
          <td>{this.props.league[r].users.displayName}</td>
        </tr>
      );
    }
    return table;
  };

  //ToDo: change table headers to match issue
  render() {
    return (
      <div className="padded-page">
        <h1></h1>
        <table className="table table-hover">
          <thead className="thead-light">
            <tr>
              <th>Team Name</th>
              <th>Wins</th>
              <th>Losses</th>
              <th>Display Name</th>
            </tr>
          </thead>
          <tbody>
                this.renderTable()
          </tbody>
        </table>
      </div>
    );
  }
}

export default CoursesTable;
